<template>
  <div class="bubble_info">
    <div class="bubble_text" ref="description">
      <h1 class="name">
        Перлиновий чай
      </h1>
      <p class="description">
        пінистий чайний напій із додаванням кульок із тапіоки
      </p>
      <button class="order_tea">Вибрати чай</button>
    </div>
    <div class="bubble_img">
      <img src="@/assets/images/bubble.png" alt="bubble" ref="bubbleImg">
    </div>
  </div>
</template>

<script>
export default {
  name: "MainInfo",
  mounted() {
    const bubble_img = this.$refs.bubbleImg;
    setTimeout(()=>{bubble_img.style.opacity = '1';
    bubble_img.style.transform = 'translateX(0)'}, 100);
    const describe = this.$refs.description;
    describe.children[2].addEventListener('mouseover', ()=>{
      describe.children[2].style.transform = 'scale(1.05)';
    })
    describe.children[2].addEventListener('mouseout', ()=>{
      describe.children[2].style.transform = 'scale(1)';
    })
    for (let j = 0; j<describe.children.length; j++) {
      setTimeout(()=>{describe.children[j].style.opacity = '1';
        describe.children[j].style.transform = 'translateX(0) scale(1)';}, (j+2)*200)
    }
  },
}
</script>

<style scoped>
.bubble_info {
  width: 60%;
  height: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.bubble_text {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-self: flex-start;
  width: 40%;
  height: 90%;
}
.bubble_text * {
  opacity: 0;
  transform: translateX(-90px);
  transition: 200ms opacity, 200ms transform;
}
.name {
  font-family: 'Montserrat Alternates',serif;
  font-style: normal;
  font-weight: 400;
  font-size: 34px;
  line-height: 59px;
  color: #000000;
}

.order_tea {
  background: linear-gradient(178.36deg, rgba(239, 255, 242, 0.81) 16.73%, #B7FFC2 124.16%);
  border: none;
  border-radius: 40px;
  transition: 350ms ease-out;
  max-width: 60%;
  height: 16%;
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 19px;
  line-height: 30px;
  color: #000000;
  cursor: pointer;
}

.order_tea:hover {
  background: linear-gradient(178.36deg, rgba(209, 255, 217, 0.81) 16.73%, #B7FFC2 124.16%);
  box-shadow: 5px 4px 30px rgba(143, 246, 168, 1);
}

.description {
  width: 200px;
  font-family: 'Montserrat',serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 132.9%;
  letter-spacing: 0.075em;
}
.bubble_img {
  display: flex;
  justify-content: flex-end;
  background: radial-gradient(50% 50% at 50% 50%, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
  margin-right: -15%;
  margin-bottom: -10%;
}
.bubble_img img {
  transition: opacity 200ms, transform 200ms;
  opacity: 0;
  transform: translateX(90px);
  width: 550px;
  height: 500px;
}
</style>