<template>
  <div class="navigation_menu">
    <div class="bubble_logo">
      <div class="logo">
        <img src="@/assets/images/logo.png" alt="logo">
        <span class="dot"></span>
      </div>
      <p class="logo_text">Bubble<br>tea</p>
    </div>
    <div class="nav__menu" ref="menu">
      <navigation-button v-for="text of this.menuList">
        {{text}}
      </navigation-button>
    </div>
  </div>
</template>

<script>
import NavigationButton from "@/components/UI/NavigationButton";
export default {
  name: "NavigationMenu",
  components: {NavigationButton},
  mounted() {
    const menu = this.$refs.menu;
    for (let j = 0; j<menu.children.length; j++) {
      setTimeout(()=>{menu.children[j].style.opacity = '1';
        menu.children[j].style.transform = 'translateY(0)';}, (j+10.5)*100)
    }
  },
  data() {
    return {
      menuList: ["Головна", "Асортимент", "Про нас", "Контакти"],
    }
  }
}
</script>

<style scoped>
 .navigation_menu {
   display: flex;
   width: 90%;
   justify-content: space-around;
   position: absolute;
   top: 5%;
 }
 .bubble_logo {
   display: flex;
   align-items: center;
 }
 .logo {
   position: relative;
   display: flex;
   justify-content: center;
   align-items: center;
   width: 50px;
   height: 50px;
   background: #000000;
   box-shadow: 0 5px 5px rgba(0, 0, 0, 0.42);
   border-radius: 18px;
   z-index: 0;
 }
 .dot {
   position: absolute;
   bottom: -7%;
   z-index: 1;
   left: 20%;
   width: 10px;
   height: 10px;
   border-radius: 50%;
   background-color: #000000;
 }
 .logo img {
   width: 25px;
   height: 25px;
   z-index: 0;
 }
 .logo_text {
   font-family: 'Montserrat Alternates', serif;
   font-style: normal;
   font-weight: 500;
   font-size: 20px;
   line-height: 28px;
   letter-spacing: 0.115em;
   color: #000000;
   text-align: center;
   margin-left: 10px;
 }
 .nav__menu {
   width: 40%;
   display: flex;
   justify-content: space-around;
   align-items: center;
 }
</style>